.links{
	margin: 10px;
	padding: 10px;
	font-size: 15px;
	box-sizing: border-box;
	overflow-wrap: break-word; 
	flex-direction:row;

	}
.links:hover{
  cursor: pointer;
}
.link-list:hover{
    animation-name: zoom;
    animation-duration: 0.5s;
}
.register{
	margin-left: 200px;
}
@keyframes zoom{
	50%{font-size: 25px;}
}


@media screen and (max-width: 500px){
    .links{
  	flex-direction: column;
  }
  .register{
  	margin-left: 10px;
  }
	}
